body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 80px;
  margin-top: 120px;
  min-width: 1100px;
  text-align: center;
  background-color: #efc9c9;
}

ol, ul {
  padding-left: 30px;
}

li {
    list-style: none;
    height: 33px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.under {
    float: left;
}

.board {
    float: left;
}

.number-top {
    float: left;
}

.number-side {
    float: left;
}

.number-side .square {
    background-color: #dacec4
}

.number-top .square {
    background-color: #dacec4
}

.board-box {
    float: left;
    border: solid;
    border-color: #555;
    margin-top: -1px;
    margin-left: -1px;
}

.side {
    float: none;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  /* font-weight: bold; */
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
  color: #555;
}

.isFilled {
    background: #f97a7a;
}

.isCrossed {
    background-image: url(./batu.png);
    background-size: contain;
}

.square-top {
    float: none;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: inline-block;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}
